import styled from 'styled-components';
import React, {useState} from 'react';

const AccountTypeWrapper = styled.section`
  font-size: 24px;
  > ul {
    display: flex;
    background: #f5f5f5;
    > li {
      width: 50%;
      text-align: center;
      padding: 16px 0 ;
      position: relative;
      color:#2c2c2c;
      &.selected{
        color:#ff8c00;
        font-weight: bold;
      }
      &.selected::after{
        content: '';
        display: block;
        height: 4px;
        background:linear-gradient(to right,#ff8c00,#ffa500);
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
      }
    }
  }
  
`;

type Props = {
  value:'-'|'+',
  onChange:(value:'-'|'+')=>void;
}
const AccountTypeSection: React.FC<Props> = (props) => {
  const typeMap ={'-':'支出','+':'收入'}
  type Keys = keyof typeof typeMap
  const [typeList] = useState<Keys[]>(['-','+'])
  const accountType= props.value
  return (
    <AccountTypeWrapper>
      <ul>
        {typeList.map(t=>
          <li key={t}
            className={accountType === t? 'selected' : ''}
              onClick={() => {props.onChange(t)}}
          >{typeMap[t]}
          </li>
        )}
      </ul>
    </AccountTypeWrapper>
  );
};

export {AccountTypeSection,AccountTypeWrapper};