import {Layout} from '../components/Layout';
import React, {useState} from 'react';
import styled from 'styled-components';
import {KeyboardSection} from './Account/KeyboardSection';
import {TagsSection} from './Account/TagsSection';
import {NoteSection} from './Account/NoteSection';
import {AccountTypeSection} from './Account/AccountTypeSection';
import {useRecords} from '../hooks/useRecords';

const MyLayout = styled(Layout)`
  display: flex;
  flex-direction: column;
  overflow: hidden;
`;

type AccountType = '-' | '+'

const defaultFormData = {
  tagIds: [] as number[],
  note: '' ,
  accountType: '-' as AccountType,
  amount: 0
}

function Account() {
  const [selected, setSelected] = useState(defaultFormData);
  const {addRecord} = useRecords()
  const onChange = (obj:Partial <typeof selected>)=>{
    setSelected({...selected, ...obj})
  }
  const submit = ()=>{
    if(addRecord(selected)) {
      alert('保存成功');
      setSelected(defaultFormData);
    }
  }
  return (
    <MyLayout>
      <AccountTypeSection value={selected.accountType}
                          onChange={accountType=>onChange({accountType})}/>
      <TagsSection value={selected.tagIds}
                   onChange={tagIds => onChange({tagIds})}
      />
      <NoteSection value={selected.note}
                   onChange={note => onChange({note})}/>
      <KeyboardSection value={selected.amount}
                       onChange={amount=>onChange({amount})}
                       onOk={()=>{submit()}}
      />
    </MyLayout>
  );
}

export {Account};