import React, {useState} from 'react';
import Icon from '../../components/Icon';
import {generateOutput} from './KeyboardSection/generateOutput';
import styled from 'styled-components';

const Wrapper = styled.section`
  display: flex;
  flex-direction: column;
  > .output {
    background: #fff;
    font-size: 36px;
    line-height: 72px;
    text-align: right;
    padding: 0 16px ;
    color:#ff4500;
    box-shadow:inset 0 5px 5px -5px rgba(0,0,0,0.25),
               inset 0 -5px 5px -5px rgba(0,0,0,0.25);
  }
  > .board {
    border: 1px solid #fff;
    > button{
      font-size: 18px;
      float: left;
      width: 25%;
      height: 64px;
      border: 1px solid #fff;
      background: linear-gradient(to right,#f5f5f5,#ffffff);
      border-radius: 4%;
      > div{
        display: flex;
        align-items: center;
        width: 100%;
        writing-mode:vertical-lr;
        font-size: 20px;
      }
      &.clear{
        height: 128px;
        float: right;
        writing-mode:vertical-lr;
      }
      &.ok{
        height: 128px;
        float: right;
        background:linear-gradient(to right,#ff8c00,#ffa500);
        color: #fff;
      }
      &.dot> span {
        display: none;
      }
      &.back> span {
        display: none;
      }
    }
    >button:active{
      background:linear-gradient(to right,#ff8c00,#ffa500);
      color: #fff;
      font-weight: bold;
      .icon{
        fill:#fff;
      }
    }
  }
`;

type Props ={
  value:number,
  onChange:(value:number)=>void,
  onOk?:()=>void
}
const KeyboardSection: React.FC<Props> = (props) => {
 //const output = props.value.toString()
  const [output,_setOutput] = useState(props.value.toString())
  const setOutput = (output: string) => {
   let newOutput:string;
    if (output.length > 16) {
      newOutput = output.slice(0, 16);
    } else if (output.length === 0) {
      newOutput = '0';
    }else{
      newOutput = output
    }
    _setOutput(newOutput)
    props.onChange(parseFloat(newOutput));
  };
  const onClickButtonWrapper = (e: React.MouseEvent) => {
    const text = (e.target as HTMLButtonElement).textContent;
    if (text === null) {return;}
    if (text === '确 定') {
      if(props.onOk){props.onOk()}
      return;
    }
    if('0123456789.'.split('').concat(['back','清 空']).indexOf(text)>=0){
      setOutput(generateOutput(text,output))
    }
  };
  return (
    <Wrapper>
      <div className="output">
        {output}
      </div>
      <div className="board clearfix"
           onClick={onClickButtonWrapper}>
        <button>7</button>
        <button>8</button>
        <button>9</button>
        <button className="clear">
          <div>清 空</div>
        </button>
        <button>4</button>
        <button>5</button>
        <button>6</button>
        <button>1</button>
        <button>2</button>
        <button>3</button>
        <button className="ok">
          <div>确 定</div>
        </button>
        <button className="dot"><Icon name="dot"/><span>.</span></button>
        <button>0</button>
        <button className="back"><Icon name="back"/><span>back</span></button>
      </div>
    </Wrapper>
  );
};

export {KeyboardSection};