import styled from 'styled-components';
import {NavLink} from 'react-router-dom';
import React from 'react';
import Icon from './Icon';

//TreeShaking 不适用于 require


const NavWrapper = styled.nav`
  line-height: 24px;
  box-shadow: 0 0 3px rgba(0,0,0,0.25);  
  background: #fff;
  > ul{
    display: flex;  
   justify-content: space-around;
    > li{
      > a {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding: 4px 0;
        color: #ff8c00;
        .icon{  
          width: 24px ;
          height: 24px;
        }
        &.selected{
          color: #f40;
          font-weight: bold;
          transform:scale(1.2);
          .icon{
            fill:#f40
          }
        }
      }
    }
  }
`;

const Nav = () => {
  return (
    <NavWrapper>
      <ul>
        <li>
          <NavLink to="/tags" activeClassName="selected">
            <Icon name="tag"/>
            标签
          </NavLink>
        </li>
        <li>
          <NavLink to="/account" activeClassName="selected">
            <Icon name="money"/>
            记一笔
          </NavLink>
        </li>
        <li>
          <NavLink to="/statistics" activeClassName="selected">
            <Icon name="chart"/>
            统计
          </NavLink>
        </li>
      </ul>
    </NavWrapper>
  );
};

export default Nav;