import {Layout} from '../components/Layout';
import React, {useState} from 'react';
import {AccountTypeSection, AccountTypeWrapper} from './Account/AccountTypeSection';
import {RecordItem, useRecords} from '../hooks/useRecords';
import {useTags} from '../hooks/useTags';
import day from 'dayjs';
import styled from 'styled-components';
import 'core-js';

const Item = styled.div`
  display: flex;
  justify-content: space-between;
  background: #fff;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 16px;
  > .tags{
    color:#ff8c00;
    font-weight: bold;
  }
  > .note{
    font-size: 14px;
    margin-right: auto;
    margin-left: 16px;
    color:darkgrey;
  }
  > .amount{
    color: #ff8c00;
    font-weight: bold;
  }
  > .date{
    margin-left: 16px;
    font-size: 14px;
    color: darkgrey;
  }
`;
const Header = styled.h3`
  font-size: 16px;
  line-height: 20px;
  padding: 10px 16px;
`;

function Statistics() {
  const [accountType, setAccountType] = useState<'-' | '+'>('-');
  const {records} = useRecords();
  const {getName} = useTags();
  const hash: { [K: string]: RecordItem[] } = {};
  const selectedRecords = records.filter(r => r.accountType === accountType);

  selectedRecords.forEach(r => {
    const key = day(r.createdAt).format('YYYY-MM-DD');
    if (!(key in hash)) {
      hash[key] = [];
    }
    hash[key].push(r);
  });
  const array = Object.entries(hash).sort((a, b) => {
    if (a[0] === b[0]) return 0;
    if (a[0] > b[0]) return -1;
    if (a[0] < b[0]) return 1;
    return 0;
  });
  return (
    <Layout>
      <AccountTypeWrapper>
        <AccountTypeSection value={accountType}
                            onChange={value => {setAccountType(value);}}
        />
      </AccountTypeWrapper>
      {array.map(([date, records]) => <div>
          <Header>
            {date}
          </Header>
          <div>
            {records.map(r => {
              return <Item>
                <div className="tags oneLine">
                  {r.tagIds.map(tagId => <span key={tagId}>{getName(tagId)} </span>)}
                </div>
                <div className="note">
                  {r.note}
                </div>
                {r.note &&
                <div className="amount">
                    ￥{r.amount}
                </div>}
                <div className="date">
                  {day(r.createdAt).format('HH:mm:ss')}
                </div>
              </Item>;
            })}
          </div>
        </div>
      )}
    </Layout>
  );
}

export {Statistics};