import React from 'react';
import {useTags} from 'hooks/useTags';
import {useParams,useHistory} from 'react-router-dom';
import {Layout} from 'components/Layout';
import Icon from 'components/Icon';
import {Button, Center, Space} from 'components/Button';
import styled from 'styled-components';
import {Input} from 'components/Input';

type Params = {
  id: string
}
const Topbar = styled.header`
  font-size: 16px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 20px;
  padding: 14px;
  :active{
  }
  . span{
    display: flex;

  }
  . {
  
  }
    
`;
const InputWrapper = styled.div`
  background: #fff;
  padding: 0 16px;
  margin-top: 16px;
`;

const Tag: React.FC = () => {
  const {findTag, updateTag, deleteTag} = useTags();
  const {id: idString} = useParams<Params>();
  const tag = findTag(parseInt(idString));
  const tagContent = (tag: { id: number, name: string }) => (
    <div>
      <InputWrapper>
        <Input label="标签名" type="text" placeholder="编辑标签名"
               value={tag.name}
               onChange={(e) => {
                 updateTag(tag.id, {name: e.target.value});
               }}
        />
      </InputWrapper>
      <Center>
        <Space/>
        <Space/>
        <Button onClick={() => deleteTag(tag.id)}>删除标签</Button>
        <Space/>
      </Center>
    </div>
  );
  const history = useHistory() // React 版的 history 代替 window 版
  const onClickBack = ()=>{
    history.goBack()
  }
  return (
    <Layout>
      <Topbar onClick={onClickBack}>
        <Icon name="left" className="xkx"/>
        <span >编辑标签</span>
        <Icon name=""/>
      </Topbar>
      {tag ? tagContent(tag) : <Center><Space/>标签不存在</Center>}
    </Layout>
  );
};

export {Tag};