import styled from 'styled-components';

const Button = styled.button`
  font-size: 18px;
  border: none;
  padding: 8px 14px;
  border-radius: 8px;
  color: #fff;
  background: #ff8c00;
  :active{
    font-weight: bold; 
  }
`;
const Center = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;
const Space = styled.div`
  height: 16px;
`;

export {Button,Center,Space}